/* General */
* {
  box-sizing: border-box;
  position: relative;
  cursor: default;
  margin: 0;
  padding: 0; }

html {
  width: 100vw;
  height: 100vh;
  position: relative;
  margin: 0;
  padding: 0;
  font-size: 62.5%; }

body {
  width: 100vw;
  margin: 0;
  padding: 0;
  font-family: "League Spartan";
  font-size: 1.6rem;
  scroll-behavior: smooth;
  overflow-x: hidden;
  background-color: #3a4764;
  background-attachment: fixed;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center; }

a {
  text-decoration: none;
  cursor: pointer; }

@media only screen and (min-width: 1000px) {
  body {
    width: 100%;
    height: 100vh; } }
.wrapper {
  width: 100vw;
  height: 100vh;
  max-width: 375px;
  max-height: 667px;
  padding: 10px;
  background-color: #dcdcd2; }

.screen {
  height: 100px;
  width: 100%;
  margin-bottom: 10px;
  background-color: rgba(128, 128, 128, 0.8);
  border-radius: 10px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  color: white; }
  .screen .result {
    background: transparent;
    color: white;
    font-size: 15px;
    border: none;
    font-weight: bold; }
  .screen .expression {
    position: absolute;
    top: 100;
    left: 0;
    right: 0;
    bottom: 0;
    margin: 10px; }

.button-box {
  width: 100%;
  height: calc(100% - 110px);
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  grid-template-rows: repeat(5, 1fr);
  grid-gap: 10px; }

.button {
  text-align: center;
  width: 80px;
  height: 50px;
  border: none;
  background-color: #503cd1;
  font-size: 24px;
  color: white;
  font-weight: bold;
  cursor: pointer;
  border-radius: 10px;
  outline: none; }

.equals {
  grid-column: 3/5; }

.reset {
  grid-column: 1/3; }

.App {
  width: 100vw;
  height: 100vh;
  margin: 0;
  padding: 0; }
